@use 'base/variables' as v;
@use 'base/mixins' as m;

.sidebar{
    opacity: 0;
    background-color: v.$azulM;
    height: 0;
    overflow: hidden;
    transition-property: opacity;
    transition-duration: 500ms;

    &.mostrar{
        opacity: 1;
        padding: 2rem;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgb(0 0 0 / .90);
        @include m.tablet {
            display: none;
        }
    }
    &.ocultar {
        opacity: 0;
    }

        @include m.tablet {
        height: 100%;
        opacity: 1;
        padding: 2rem;
        background-color: v.$azulM;
        }
        .contenedor-sidebar{
            display: flex;
            justify-content: space-between;
            align-items: center;
            h2{
                color: v.$blanco;
                margin: 0;
                text-align: center;
            }  
            .cerrar-menu{
                display: flex;
                justify-content: center;
                height: 2rem;
                width: 2rem;
                img{
                    &:hover{
                        cursor: pointer;
                    }
                }
                @include m.tablet {
                    display: none;
                }
            }
        }

    .sidebar-nav{
        margin-top: 5rem;

        a{
            color: v.$blanco;
            display: block;
            margin-bottom: 3rem;
            padding: .7rem;
            border-radius: .5rem;
            transition-property: background-color;
            transition-duration: 300ms;
            text-transform: uppercase;
            font-size: 2.6rem;
            font-weight: 700;
            text-align: center;

            &:hover,
            &.activo {
                background-color: lighten(v.$azulM, 20%);
            }

            @include m.tablet {
                font-size: 1.7rem;
                margin-bottom: 2rem;
                text-align: left;
                
            }
        }
        
    }
    .cerrar-sesion-mobile{
        margin-top: 10rem;
        .cerrar-sesion {
            @include m.boton (v.$rojo, v.$blanco);
        }

        @include m.tablet {
            display: none;
        }
    }
}