html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #000;
  font-size: 3rem;
  text-align: center;
}

.descripcion-pagina {
  text-align: center;
}

/** Media Queries **/
.formulario {
  margin-top: 5rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 10rem;
}
.formulario .campo input {
  flex: 1;
  padding: 1rem;
  border: none;
  background: #d8dadf;
  border-radius: 1.2rem;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 2rem;
  color: #000;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}

.alerta {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 900;
  margin: 1rem 0;
  font-size: 1.4rem;
  max-width: 80rem;
  text-align: center;
}
.alerta.error {
  background-color: #ffc3c3;
  border-left: 0.6rem solid #a90000;
  border-right: 0.6rem solid #a90000;
  border-radius: 0.8rem;
  color: #a90000;
}
.alerta.exito {
  background-color: #92ff92;
  border-left: 0.6rem solid #007800;
  border-right: 0.6rem solid #007800;
  border-radius: 0.8rem;
  color: #007800;
}

.login {
  margin-top: 5rem;
}
.login .uptask {
  background: linear-gradient(to right, #0891B2 0%, #240853 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 7rem;
}
@media (min-width: 768px) {
  .login .uptask {
    font-size: 10rem;
  }
}
.login input[type=submit] {
  background-color: rgba(11, 24, 83, 0.968627451);
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  border-radius: 0.7rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  /* Estilos del botón al hacer clic */
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: rgba(5, 11, 38, 0.968627451);
  cursor: pointer;
}
.login input[type=submit]:active {
  transform: translateY(8px);
  /* Mover el botón ligeramente hacia abajo al hacer clic */
  box-shadow: none;
  /* Quitar la sombra al hacer clic */
}
.login .acciones a {
  color: rgba(11, 24, 83, 0.968627451);
  font-weight: 700;
}

