.sidebar {
  opacity: 0;
  background-color: rgba(11, 24, 83, 0.968627451);
  height: 0;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 500ms;
}
.sidebar.mostrar {
  opacity: 1;
  padding: 2rem;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.9);
}
@media (min-width: 768px) {
  .sidebar.mostrar {
    display: none;
  }
}
.sidebar.ocultar {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar {
    height: 100%;
    opacity: 1;
    padding: 2rem;
    background-color: rgba(11, 24, 83, 0.968627451);
  }
}
.sidebar .contenedor-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .contenedor-sidebar h2 {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
.sidebar .contenedor-sidebar .cerrar-menu {
  display: flex;
  justify-content: center;
  height: 2rem;
  width: 2rem;
}
.sidebar .contenedor-sidebar .cerrar-menu img:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .contenedor-sidebar .cerrar-menu {
    display: none;
  }
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 3rem;
  padding: 0.7rem;
  border-radius: 0.5rem;
  transition-property: background-color;
  transition-duration: 300ms;
  text-transform: uppercase;
  font-size: 2.6rem;
  font-weight: 700;
  text-align: center;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.activo {
  background-color: rgba(23, 50, 173, 0.968627451);
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    font-size: 1.7rem;
    margin-bottom: 2rem;
